import React from "react";
import { ZpageFooter } from "zerod";
import { Icon} from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZpageFooter/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			footerLinks=[{
                key: "hua-cloud",
                title: "华云中盛科技有限公司",
                href: "http://www.hua-cloud.com.cn/",
                blankTarget: true,
            },
            {
                key: "szhcf",
                title: "华成峰集团",
                href: "http://www.szhcf.com.cn/",
                blankTarget: true,
            }];
            footerCopyright = (
                <div>
                    Copyright <Icon type="copyright" /> {new Date().getFullYear()} 华云中盛-政务事业部技术团队出品
                </div>
            );
            render() {
                return (
                    <ZpageFooter
                        links={this.footerLinks}
                        copyright={this.footerCopyright}
                    />
                );
            }
		}
		return (
			<Myjavascript />
		);
	},
});
